import '@devsnc/sn-search-genius-card-assist';
import { transformProps } from './utils';

const view = state => {
    const {
        properties,
        behaviors: { mediaQuery },
    } = state;
    const isMobile = mediaQuery.mobileQuery;
    const userConfig = transformProps(properties, isMobile);
    return <sn-search-genius-card-assist {...userConfig} />;
};

export default view;
