import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import view from './view';

/**
 * Card component to display Catalog Genius Results.
 *
 * ```jsx
 * <sn-search-genius-catalog-card
 *     identifier="Benefits"
 *     marker="news-outline"
 *     markerType="icon"
 *     heading="Tuition preapproval"
 *     primaryDescription="This is a description text"
 *     secondaryDescription="This is a secondary text"
 *     actions={[
 *         {
 *             assignmentId: '42fd3e5273511010c342d5fdbdf6a70e',
 *             name: 'view',
 *             label: 'View',
 *         },
 *     ]}
 *     model={{
 *         name: 'Tuition preapproval',
 *         description: 'This is a description text',
 *         sysId: 'fd826bf03710200044e0bfc8bcbe5d30',
 *         sys_id: 'fd826bf03710200044e0bfc8bcbe5d30',
 *         table: 'sc_cat_item',
 *     }}
 * />
 * ```
 *
 * @seismicElement sn-search-genius-catalog-card
 * @summary Card component to display Catalog Genius Results.
 */
createCustomElement('sn-search-genius-catalog-card', {
    renderer: { type: snabbdom },
    view,
    initialState: {
        timerId: null,
        time: null,
    },
    properties: {
        /**
         * This property specifies the identifier text to render.
         * @type {string}
         */
        identifier: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies the icon or image source to use for the card.
         * If no icon and image is defined, then the component will default to the icon `pencil-page-outline`.
         * @type {string}
         */
        marker: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Defines the type of marker to render the appropriate component.
         * @type ('image'|'icon')
         */
        markerType: {
            default: 'image',
            schema: {
                type: 'string',
                enum: ['image', 'icon'],
            },
        },
        /**
         * Alternative text description of the image.
         * @type {string}
         */
        imageAlt: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * This property specifies the heading for the card.
         * @type {string}
         */
        heading: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Primary description for the catalog.
         * @type {string}
         */
        primaryDescription: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Secondary description for the catalog.
         * Renders below the primary description.
         * @type {string}
         */
        secondaryDescription: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * This property specifies the actions to render in the genius card at the footer.
         * Declarative actions are used to configure actions.
         * @type {array}
         */
        actions: {
            default: [],
            schema: {
                type: 'array',
            },
        },
        /**
         * This is an object that contains data associated to the card.
         * @type {object}
         */
        model: {
            default: [],
            schema: {
                type: 'object',
            },
        },
    },
});
