import compact from 'lodash/compact';

const DEFAULT_ICON = 'pencil-page-outline';

/* eslint-disable import/prefer-default-export */
export const transformProps = properties => {
    const {
        identifier,
        marker,
        markerType,
        imageAlt,
        heading,
        primaryDescription,
        secondaryDescription,
        actions,
        model,
    } = properties;

    const value = marker || DEFAULT_ICON;
    const type = value === DEFAULT_ICON ? 'icon' : markerType;
    const descriptionValues = compact([
        primaryDescription,
        secondaryDescription,
    ]);

    return {
        actions,
        model,
        identifier,
        heading,
        marker: {
            type,
            value,
            alt: imageAlt,
        },
        description: {
            type: 'stacked',
            values: descriptionValues,
        },
    };
};
