import compact from 'lodash/compact';

/* eslint-disable import/prefer-default-export */
const MAX_COMPACT_PERSON_INFO = 2;
const HORIZONTAL = 'horizontal';
const VERTICAL = 'vertical';

export const transformProps = (properties, isMobile) => {
    const {
        identifier,
        identifierCriticalText,
        avatar,
        avatarName,
        heading,
        primaryDescription,
        secondaryDescription,
        actions,
        model,
        additionalInfoLabelOne,
        additionalInfoValueOne,
        additionalInfoLabelTwo,
        additionalInfoValueTwo,
        additionalInfoLabelThree,
        additionalInfoValueThree,
        additionalInfoLabelFour,
        additionalInfoValueFour,
    } = properties;

    let align = HORIZONTAL;
    let labelValues = [];

    if (additionalInfoLabelOne) {
        labelValues.push({
            label: additionalInfoLabelOne,
            value: additionalInfoValueOne,
        });
    }

    if (additionalInfoLabelTwo) {
        labelValues.push({
            label: additionalInfoLabelTwo,
            value: additionalInfoValueTwo,
        });
    }

    if (additionalInfoLabelThree) {
        labelValues.push({
            label: additionalInfoLabelThree,
            value: additionalInfoValueThree,
        });
    }

    if (additionalInfoLabelFour) {
        labelValues.push({
            label: additionalInfoLabelFour,
            value: additionalInfoValueFour,
        });
    }

    if (isMobile) {
        align = VERTICAL;
        labelValues = labelValues.slice(0, MAX_COMPACT_PERSON_INFO);
    }

    const descriptionValues = compact([
        primaryDescription,
        secondaryDescription,
    ]);

    return {
        actions,
        model,
        identifier,
        identifierCriticalText,
        heading,
        marker: {
            type: 'avatar',
            value: avatar,
            alt: avatarName,
        },
        description: {
            type: 'stacked',
            values: descriptionValues,
        },
        labelValuesInfo: {
            labelValues,
            align,
        },
    };
};
