/* eslint-disable import/prefer-default-export */
import isEmpty from 'lodash/isEmpty';
import { t } from 'sn-translate';

const formatIdentifier = identifier =>
    isEmpty(identifier) ? identifier : t(`Extracted from '{0}'`, identifier);

export const transformProps = properties => {
    const { identifier, heading, description, actions, model } = properties;
    const formattedIdentifier = formatIdentifier(identifier);

    return {
        actions,
        model,
        identifier: formattedIdentifier,
        heading,
        description: {
            type: 'html',
            values: [description],
        },
    };
};
