import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';

/**
 * Card component to display Q&A Genius Results.
 *
 * ```jsx
 *  <sn-search-genius-qa-card
 *      identifier={identifier}
 *      heading={heading}
 *      description={description}
 *      actions={actions}
 *      model={model}
 *  />
 * ```
 *
 * @seismicElement sn-search-genius-qa-card
 * @summary Card component to display Q&A Genius Results.
 */
createCustomElement('sn-search-genius-qa-card', {
    renderer: { type: snabbdom },
    view,
    properties: {
        /**
         * This property specifies the identifier text to render.
         * If an identifier is specified, it will always be interpolated in this template:
         * Extracted from '${identifier}'.
         * @type {string}
         */
        identifier: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * This property specifies the heading for the card.
         * @type {string}
         */
        heading: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies the description text(s) for the genius card. It supports HTML content.
         * @type {string}
         */
        description: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * This property specifies the actions to render in the genius card at the footer.
         * Declarative actions are used to configure actions.
         * @type {array}
         */
        actions: {
            default: [],
            schema: {
                type: 'array',
            },
        },
        /**
         * This is an object that contains data associated to the card.
         * @type {object}
         */
        model: {
            default: [],
            schema: {
                type: 'object',
            },
        },
    },
});
