import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import mediaQueryBehavior from '@servicenow/behavior-media-query';

import view from './view';

/**
 * Card component to display User Genius Results.
 *
 * ```jsx
 * <sn-search-genius-user-card
 *     identifier="San Diego, USA"
 *     avatar="45567543231112aa.iix"
 *     heading="System Administrator"
 *     identifierCriticalText="4:30 pm"
 *     primaryDescription="Software Engineer, Dev - Search UX"
 *     secondaryDescription="Web Interfaces"
 *     additionalInfoLabelOne="E-mail"
 *     additionalInfoValueOne="samina.arizmendi@acmetech.com"
 *     additionalInfoLabelTwo="Mobile"
 *     additionalInfoValueTwo="+91 (987)654-3210"
 *     additionalInfoLabelThree="Office"
 *     additionalInfoValueThree="+91 (987)654-3211"
 *     additionalInfoLabelFour="Manager"
 *     additionalInfoValueFour="Abel Tutor"
 *     actions={[
 *         {
 *             assignmentId: '42fd3e5273511010c342d5fdbdf6a70e',
 *             name: 'activate_user',
 *             label: 'Activate',
 *         },
 *         {
 *             assignmentId: 'e3a173c073151010c342d5fdbdf6a7a2',
 *             name: 'call_number',
 *             label: 'Call Number',
 *         },
 *     ]}
 *     model={{
 *         name: 'Mac Marksberry',
 *         home_phone: '8581234567',
 *         location: 'Bockenheimer LandstraÃŸe 123, Frankfurt',
 *         department: 'Customer Support',
 *         sysId: 'fd826bf03710200044e0bfc8bcbe5d30',
 *         sys_id: 'fd826bf03710200044e0bfc8bcbe5d30',
 *         table: 'sys_user',
 *     }}
 * />
 * ```
 *
 * @seismicElement sn-search-genius-user-card
 * @summary Card component to display User Genius Results.
 */
createCustomElement('sn-search-genius-user-card', {
    renderer: { type: snabbdom },
    view,
    initialState: {
        timerId: null,
        time: null,
    },
    properties: {
        /**
         * This property specifies the identifier text to render.
         * @type {string}
         */
        identifier: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * This property specifies critical text to render next to the identifier.
         * This text is rendered in red.
         * @type {string}
         */
        identifierCriticalText: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies the avatar source for the Person genius card.
         * Renders `<now-avatar />`
         * @type {string}
         */
        avatar: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Name of the user that the avatar represents.
         * If no `avatar` is provided, then the initials of the first
         * two names that are provided in the string appear in the avatar.
         * @type {string}
         */
        avatarName: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * This property specifies the heading for the card.
         * @type {string}
         */
        heading: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Description to place on the first line.
         * @type {string}
         */
        primaryDescription: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Description to place on the second line.
         * @type {string}
         */
        secondaryDescription: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies the first person additional information label.
         * @type {string}
         */
        additionalInfoLabelOne: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies the first person additional information value.
         * @type {string}
         */
        additionalInfoValueOne: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies the second person additional information label.
         * @type {string}
         */
        additionalInfoLabelTwo: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies the second person additional information value.
         * @type {string}
         */
        additionalInfoValueTwo: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies the third person additional information label.
         * @type {string}
         */
        additionalInfoLabelThree: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies the third person additional information value.
         * @type {string}
         */
        additionalInfoValueThree: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies the fourth person additional information label.
         * @type {string}
         */
        additionalInfoLabelFour: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies the fourth person additional information value.
         * @type {string}
         */
        additionalInfoValueFour: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * This property specifies the actions to render in the genius card at the footer.
         * Declarative actions are used to configure actions.
         * @type {array}
         */
        actions: {
            default: [],
            schema: {
                type: 'array',
            },
        },
        /**
         * This is an object that contains data associated to the card.
         * @type {object}
         */
        model: {
            default: [],
            schema: {
                type: 'object',
            },
        },
    },
    behaviors: [
        {
            behavior: mediaQueryBehavior,
            options: {
                mobileQuery: '(max-width: 600px)',
            },
        },
    ],
});
